.hero {
  background: linear-gradient(0deg, #fffc 0%, #fff0 40%), url("search_hero_bg.e7dc04b9.jpg") center / cover;
}

.hero .content-stripe {
  grid-template-rows: 1fr auto;
}

.hero sub {
  color: #ce0606;
  font-size: .5em;
}

.filter-options h3 {
  margin-block: 20px 15px;
}

.filter-options label {
  margin-inline: 5px;
}

.filter-options label, .filter-options input {
  display: inline;
}

.filter-options input, .filter-options select {
  height: 25px;
}

.filter-options select {
  border: none;
  margin-right: 15px;
  padding: 5px;
}

.search-query-text {
  margin-block: 25px;
  font-weight: 500;
}

.search-results {
  flex-direction: column;
  display: flex;
}

.result-card {
  cursor: pointer;
  grid-template-rows: 3em auto 3em;
  grid-template-columns: 30% auto;
  align-items: center;
  column-gap: 30px;
  margin-bottom: 15px;
  padding-right: 40px;
  display: grid;
  box-shadow: 0 0 4px 1px #d7d7d7;
}

.result-card:nth-child(2n) {
  background-color: #eee;
}

.card-bg {
  clip-path: polygon(0 0, 100% 0, 90% 100%, 0 100%);
  grid-area: 1 / 1 / 3 / 2;
  width: 100%;
  height: 100%;
}

.card-bg img {
  object-fit: cover;
  width: 100%;
  height: 210px;
}

.card-header {
  color: #ff8c00;
  margin-top: 10px;
}

.card-content {
  margin-top: 10px;
}

.summary {
  text-align: justify;
  line-height: 1.3em;
}

.resources {
  margin-top: 20px;
  margin-left: -15px;
}

.resources ul {
  gap: 5px;
  margin-top: 10px;
  display: flex;
}

.resources li {
  width: 30px;
  height: 30px;
}

.resources img {
  width: 100%;
}

.card-footer {
  grid-area: 3 / 1 / 4 / 3;
  padding-left: 20px;
}

.leader-name {
  font-weight: 700;
}

.pagination nav {
  justify-content: center;
  gap: 20px;
  margin-block: 10px 20px;
  display: flex;
}

.pagination li {
  -webkit-user-select: none;
  user-select: none;
  border: 1px solid #bbb;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  min-width: 60px;
  line-height: 0;
  display: flex;
  box-shadow: 0 0 5px 1px #c3c3c3;
}

.pagination li:nth-child(2) {
  box-shadow: none;
  border-inline: initial;
  border-radius: 0;
}

.pagination p {
  font-size: large;
}

.pagination a {
  color: #000;
  background: none;
  padding: 25px;
}

li:has(a.disabled) {
  border-color: #fff;
}

a.disabled {
  color: #ddd;
  cursor: default;
}
